import React, { useEffect, useRef } from 'react';
import Typewriter from 'typewriter-effect/dist/core';
import "./Landing.css";

export default function Landing() {

    const landingEl = useRef(null);
    const messageEl = useRef(null);

    useEffect(() => {
        let colors = ["#338eda", "#5bc0de", "#ff8c37", "#33d6a6", "#a633d6"];
        let i = 0;

        function change() {
            landingEl.current.style.backgroundColor = colors[i];
            // messageEl.current.innerHTML = messages[j];
            i++;


            if (i > colors.length - 1) {
                i = 0;
            }
        }
        setInterval(change, 2000);
        new Typewriter('#typewriter', {
            strings: ["Getting things ready...", "Running field tests...", "Tweaking the system...", "Hacking the machine..."],
            autoStart: true,
            loop: true
        });
    }, [])

    return (
        <div ref={landingEl} className="landing">
            <div className="landing__content">
                <img src="https://assets.hackclub.com/flag-standalone.svg" height="100vh" alt="hackclub " />
                <h2>KIIT Hack Club</h2>
                <p ref={messageEl} id="typewriter" className="landing__message">Hacking the machine...</p>
                <p className="landing__comingSoon">We will be up soon, keep an eye on this space!   </p>
            </div>
        </div>
    )
}
